.popup__text {
  width: 100%;
  height: 27px;
  margin: 0;
  padding: 0;
  border: none;
  border-bottom: 1px solid rgba(0, 0, 0, 0.2);
  font-weight: normal;
  font-size: 14px;
  line-height: 1.214;
}
.popup__text:last-child {
  margin: 30px 0 0 0;
}
.popup__text::placeholder {
  color: #C4C4C4;
}